<!--  -->
<template>
  <div class="app" >
    <div class="listwrap" v-if="pagestatus">
      <div class="left">
        <img src="https://head.dziqian.com/avtaurl.png" alt />
      </div>
      <div class="right">
        <div>{{name}}</div>
        <div>
          <van-rate v-model="score" readonly color="#ee0a24" />
        </div>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      class="van-list"
      :immediate-check="immediate"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="box" v-for="(item,index) in list" :key="index" @click="item.type==0?item.type=1:item.type=0">
        <div class="companywrap">
          <div class="company">{{item.client}}</div>
          <div class="score">
            <van-rate v-model="item.score" readonly />
          </div>
        </div>
        <div class="Labelwrap" v-if="item.tag_id.length>0" >
          <div  class="littleLong">
            <span class="Label" v-for="(tag_iditem,ind) in item.tag_id" :key="ind">{{tag_iditem}}</span>
            <van-icon name="play" size='18' class="playicon" v-show="item.content"  :style="{transform:item.type?'rotate(0deg)':'rotate(90deg)'}" />
          </div>
          <!-- <span class="Label" v-for="(tag_iditem,ind) in item.tag_id" :key="ind">{{tag_iditem}}</span> -->
        </div>
        <div class="Labelwrap Labelwrapnodata" v-if="item.tag_id.length==0">
           该公司暂无标签评价
        </div>
        <div class="content" v-show="!item.type">{{item.content}}</div>
        <div class="time" >{{item.createtime}}</div>
      </div>
    </van-list>
  </div>
</template>

<script>
import axios from "./../axios/axios";
export default {
  data() {
    return {
      list: [],
      name:'',
      score:5,
      loading: false,
      finished: false,
      immediate: false,
      value: 5,
      page:0,
      pagestatus:false,
      showmosttext:false
    };
  },
  created() {
       if (!this.$route.params.uid && !localStorage.getItem('uid')) {
      this.$router.push({
        path: "/tip",
        query: { tip: "uid参数有误", type: "error" }
      });
    } else {
      if (this.$route.params.uid){
localStorage.setItem("uid", this.$route.params.uid);
      }
    this.onLoad()
    }
  },
  // mounted() {

  // },
  methods: {
    onLoad() {
      this.page++;
      let uid = this.$route.params.uid || localStorage.getItem('uid')
      let param = {page:this.page,uid}
  axios.get("reviewdetaillist", param).then(res =>{
      
    console.log(res.data.data.name,uid)
      let listdata = res.data.data.list;
    if (res.data.code){
       this.loading = false;
       if (this.page == 1){
         this.list = listdata
       }else{
         this.list = [...this.list, ...listdata]
       }
         this.name = res.data.data.name
         this.score = res.data.data.score
      listdata.length == 0
        ? (this.finished = true)
        : (this.finished = false);
        this.pagestatus = true
    }else {
            this.$toast.fail(res.data.msg ? res.data.msg : "参数有误");
           if (res.data.data== 'timeout'){
            localStorage.removeItem('token')
            this.$router.push({name:'login'})
            }
       }
  })

    },
    showmost(){
      this.showmosttext = !this.showmosttext
    }
  }
};
</script>
<style  scoped>
.app {
  width: 100%;
  height: 100%;
}
.listwrap {
  display: flex;
  width: 95%;
  margin: 10px auto;
  justify-content: space-between;
}
.littleLong{
  position: relative;
  /* width: 200%; */
  overflow: hidden;
}
.playicon{
  position: absolute;
  right: 2px;
  top: 4px;
  transition: all 0.3s
}

.left {
  width: 50px;
  margin-left: 14px;
}
.content{
  color: rgba(233, 42, 17, 0.938);
  margin-bottom: 8px;
}
.left img {
  width: 100%;
}
.right {
  width: calc(100vw - 100px);
  font-size: 18px;
}
.right > div {
  margin: 4px 0;
}
.box{
  width: 100%;
  margin: 0 auto;
   /* border-top: 1px solid #e9e6e6; */
   box-sizing: border-box;
   padding: 10px;
   border-radius: 4px;
}
.box:nth-child(2n+1){
  background: #f5f4f4
}
.van-list{
  height: 100%;
}
.company{
  font-size: 16px /*no*/;
  /* width: 65%; */
  text-align: left
}
.score{

}
.Labelwrapnodata{
  color: #a3a0a0
}
.Labelwrap{
  margin: 8px 0;
   transition: all 0.3s
}
.FixedHeight{
  height: 29px;
  overflow: hidden;
  margin: 8px 0;
  transition: all 0.3s
}
.Label{
  display: inline-block;
  box-sizing: border-box;
  /* width: 24%; */
  text-align: left;
  padding:0 2px;
  border: 1px solid green;
  border-radius: 2px;
  margin:4px 10px 4px 0 ;
}
.companywrap{
  display: flex;
  justify-content: space-between;
 
}
</style>


